.advantages {
  padding-top: 110px;


  &__list {
    list-style: none;
    width: 1170px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    margin-right: auto;
    margin-left: auto;
  }

  &__item {
    width: 570px;
    min-height: 299px;
    padding: 40px 42px 49px 44px;
    border: 1px solid #EBEBEB;
    box-sizing: border-box;
    border-left: 3px solid #00B2FF;
    margin-bottom: 30px;

    & svg {
      fill: #00B2FF;
    }

    & h3 {
      font-family: "Roboto Condensed", sans-serif;
      font-weight: 400;
      font-size: 24px;
      line-height: 34px;
      margin-top: 17px;
      margin-bottom: 16px;
      color: #00B2FF;
    }

    & p {
      font-size: 16px;
      line-height: 22px;
    }

    &:hover {
      background-color: #00B2FF;

      & svg {
        fill: #FFFFFF;
      }

      & h3,
      & p {
        color: #FFFFFF;
      }
    }
  }


  &__item--stopwatch svg {
    width: 29px;
    height: 35px;
  }

  &__item--hired svg {
    width: 35px;
    height: 35px;
  }

  &__item--detective svg {
    width: 36px;
    height: 31px;
  }

  &__item--tag svg {
    width: 35px;
    height: 35px;
  }

}

@media (max-width: $tablet-width) {
  .advantages {
    width: 100%;
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 55px;
    box-sizing: border-box;

    &__list {
      width: 100%;
    }

    &__item {
      width: calc(50% - 15px);
      border-left: 2px solid #00B2FF;
      padding: 23px 20px 23px 25px;
      min-height: 232px;

      & h3 {
        margin-top: 3px;
        margin-bottom: 9px;
      }

      & p {
        margin: 0;
      }
    }

    &__item--stopwatch svg {
      width: 29.38px;
      height: 35.46px;
    }

    &__item--hired svg {
      width: 31.72px;
      height: 32.18px;
    }


    &__item--detective {
      & svg {
        width: 30.31px;
        height: 26.1px;
      }

      & h3 {
        margin-top: 9px;
        margin-bottom: 15px;
      }
    }

    &__item--tag svg {
      width: 32.18px;
      height: 32.18px;
    }

  }
}

@media (max-width: $mobile-width) {
  .advantages {
    padding-left: 14px;
    padding-right: 16px;
    padding-top: 38px;

    &__list {
      flex-direction: column;
    }

    &__item {
      width: 100%;
      margin-bottom: 21px;
      padding: 23px 13px 23px 25px;

      & h3 {
        font-size: 20px;
        line-height: 28px;
        margin-top: 10px;
        margin-bottom: 9px;
      }
    }

    &__item--hired {
      padding-right: 10px;
    }

    &__item--detective {
      padding-right: 25px;

      & h3 {
        margin-bottom: -6px;
      }
    }
  }
}
